body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.app {
  width: 100%;
  margin-top: 140px;
  /*text-align: center;*/
  /*min-height: 500px*/
}

.title {
  font-size: 2.0em;
  position: absolute;
  left: 10px;
  top: 10px;
  background-color: #3351c6;
  /*background-color: #133480;*/
  /*text-decoration: underline;*/
}

.flex-column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

.message-header {
  font-size: 1.2em;
  white-space: nowrap;
  text-overflow: ellipsis;
  max-width: 300px;
  overflow: hidden;
}

.message-to-user {
  transition: all 0.5s ease;
  font-weight: 500;
  position: fixed;
  width: 50%;
  left: 25%;
  height: 60px;
}
